const { FileService } = require('../../../services');

module.exports = async (req, res) => {
  try {
    console.log(req.body)
    await FileService.uploadFile ({
      ...req.body,
      ownerId: req.user._id,
    });
    res.success();
  } catch (err) {
    console.log(err);
    res.error({
      message: err,
    });
  }
};
